import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PinterestService } from '../services/pinterest.service';

@Component({
  selector: 'app-o-auth2-callback',
  templateUrl: './o-auth2-callback.component.html',
  styleUrls: ['./o-auth2-callback.component.scss']
})
export class OAuth2CallbackComponent implements OnInit {

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private service: PinterestService,
  ) { }

  async ngOnInit() {
    try {
      let queryParams = this.route.queryParams;
      if (queryParams['code']) {
        console.log('*** ACCESS CODE ***', queryParams['code']);
        this.service.setAccessCode(queryParams['code']);
      } else {
        this.router.navigate(['/login']);
      }
    } catch (error) {
      console.error(error);      
    }
  }

}
